<template>
  <div class="panel">

    <h1>پروفایل کاربر</h1>

    <form @submit.prevent="onFormSubmit">

      <div class="row">
        <div class="col-md mt-1">
          <label class="form-label">
            نام
          </label>
          <input type="text" class="form-control" v-model="model.usrNickName"/>
        </div>

        <div class="col-md mt-1">
          <label class="form-label">
            نام خانوادگی
          </label>
          <input type="text" class="form-control" v-model="model.usrFamily"/>
        </div>
      </div>

      <div class="mt-1">
        <label class="form-label">
          ایمیل
        </label>
        <input type="email" class="form-control" v-model="model.usrEmail"/>
      </div>

      <div class="mt-3 d-flex flex-row-reverse">

        <button :type="working ? 'button' : 'submit'" class="btn btn-primary">
          <span class="fa" :class="working ? 'fa-spin fa-sync' : 'fa-check'"></span>
          <span class="ms-1">بروز رسانی</span>
        </button>

      </div>

    </form>
  </div>
</template>

<script>
import { dispatchNotification } from '../../library/stores/notifications'

export default {
  name: 'Profile',
  data: () => ({
    working: false,
    model: {},
  }),
  methods: {
    onFormSubmit() {
      if (this.working) {
        return
      }

      this.working = true
      this.$fetch('/users', 'PATCH', this.model)
          .then(() => {
            this.$fetch(`/users/${this.$store.state.user.usrId}`)
                .then(x => {
                  this.$store.commit('setUser', x.data)
                  dispatchNotification('پروفایل شما با موفقیت بروز شد')
                })
          })
          .finally(() => this.working = false)
    }
  },
  mounted() {
    this.model = { ...this.$store.state.user }
  }
}
</script>

